html, body{
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(83, 83, 83);
  color: rgb(0, 251, 255);
  font-size: 20px;
}

.headerSection{
   /* height:100px; */
   position: fixed;
   width: 100%;
   top:0px;
   background-color: pink;

   margin: 0;
   padding: 0;

}

li{
  list-style-type: none;
  
}

.headerSection li{
  
  background-color: rgb(0, 202, 206);
  /* height: 100%; */
  width: 20%;
  color: black;
  margin: 0;
  padding: 0;
  height: 50px;
  text-align: center;
  font-size: 20pt;
  float: left;

  overflow: hidden;

}

.headerSection li:hover{
  transition: 0.5s;
  background-color: purple;
  color: white;
}

.footerSection{
  position: fixed;
  bottom: 0px;
  width:100%;
  background-color: rgb(0, 251, 255);

  margin: 0;
  padding: 0;
}


.space{
  display:block;
  height: 100px;
  /* background-color: red; */
}

ul{
  list-style-type: none;
  width: 100%;
  margin: 0;
  padding: 0;
}

.footerSection li{
  list-style-type: none;
  float: right;
  padding: 0px 25px 0px 25px;

}

.container{
  padding: 5%;
}

#prof_pic{
  border: 2px solid rgb(0, 251, 255);
  border-radius: 50%;
}

a:visited{
  color: inherit;
  text-decoration-line: none;
}
